import React from 'react'
import { Link } from "react-scroll";
import './NavBar.css'

const NavBar = (props) => {
    return (
    <div className={props.hidden ? "NavBar ns active" : "NavBar ns"}>
        <Link
            to="home"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-50}
            duration={500}
            >Inicio
        </Link>
        <Link
            to="About"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={0}
            duration={500}
            >Sobre mi
        </Link>
        <Link
            to="projects"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={0}
            duration={500}
            >Portafolio
        </Link>
       
    </div>
    )
}
export default NavBar