import React from 'react'
import "./Footer.css"
import { animateScroll as scroll } from 'react-scroll'
import {FaAngleDoubleUp, FaLinkedin, FaInstagramSquare, FaGithubSquare} from 'react-icons/fa'; 
const index = () => {
    const scrollToTop = () =>{
        scroll.scrollToTop();
    }
    return (
        <div className="Footer">
            <div onClick={scrollToTop} className="upButton">
                <FaAngleDoubleUp/>
            </div>
            <div className="footer-content">
                <div className="Footer-social">
                    <a rel="noreferrer" target="_blank" href="https://www.linkedin.com/in/luiscarlsoto/"><FaLinkedin/></a>  
                    <a rel="noreferrer" target="_blank" href="https://www.instagram.com/luiscarlsoto/"><FaInstagramSquare/></a> 
                    <a rel="noreferrer" target="_blank" href="https://github.com/luiscarlsoto/"><FaGithubSquare/></a>
                </div>
                <div className="Footer-info">
                        LUIS SOTO © 2021
                </div>
            </div>
        </div>
    )
}

export default index
