import React, {useState} from 'react'
import profileImg from '../../assets/profile.jpg'
import './About.css'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ImportantDevicesIcon from '@material-ui/icons/ImportantDevices';
import ExtensionIcon from '@material-ui/icons/Extension';
import NetworkCheckIcon from '@material-ui/icons/NetworkCheck';
import { FaReact, FaNodeJs, FaHtml5, FaCss3Alt, FaJsSquare } from 'react-icons/fa';
import { SiTypescript } from 'react-icons/si'
const About = () => {
    const [open, setOpen] = useState(false)
    const openHandle = () =>{
        setOpen(!open)
    }
    return (
        <div className={open ? "container flex-row paper ns About open" : "container flex-row paper ns About "} id="About">
                    <div className="About-section">
                        <div className="title">
                            <h2>About</h2>
                        </div>
                        <div className="About-s-body">
                            <div>
                                <img className="profileImg" alt="profileImg" src={profileImg} />
                            </div>
                            <div className="About-me">
                                <h3>Quien soy?</h3>
                                Soy un desarrollador front-end residenciado en venezuela.
                                Tengo una increible pasión por los UI effects, animaciones y me gusta hacer las cosas de 
                                formas intuitivas y dinámica para el usuario. <br/>
                                Contáctame y hagamos algo especial!.
                            </div>
                        </div>
                        <div className="About-s-footer">
                            <button onClick={openHandle} className="About-Button-More"><span  className="ArrowMoreIcon"><ExpandMoreIcon/></span></button>
                        </div>

                    </div>
                <div className="About-section-2">
                <div className="About-ss">
                        <h3>Servicios</h3>
                        <div className="About-services">
                            <div>
                                <span><NetworkCheckIcon/></span>
                                <h4>Rapidéz</h4>
                                Mi prioridad es hacer menor el tiempo de carga y las interacciones
                                libres de retraso.
                            </div>
                            <div>
                                <span><ImportantDevicesIcon/></span>
                                <h4>Responsive</h4>
                                Mis páginas web funcionan en cualquier dispositivo, sean grandes o pequeños.
                            </div>
                            <div>
                                <span><ExtensionIcon/></span>
                                <h4>Intuitivo</h4>
                                Preferencia en volver una experencia sencilla y completa al usuario, diseños intutivos.
                            </div>
                        </div>
                    </div>
                    <div className="About-st">
                        <h3>Tecnologias</h3>
                        <div className="About-Tech">
                            <div>
                                <span><FaReact/></span>
                                <h4>ReactJS</h4>
                            </div>
                            <div>
                                <span><FaNodeJs/></span>
                                <h4>NodeJS</h4>
                            </div>
                            <div>
                                <span><FaJsSquare/></span>
                                <h4>JavaScript</h4>
                            </div>
                            <div>
                                <span><SiTypescript/></span>
                                <h4>TypeScript</h4>
                            </div>
                            <div>
                                <span><FaCss3Alt/></span>
                                <h4>Css3</h4>
                            </div>
                            <div>
                                <span><FaHtml5/></span>
                                <h4>Html5</h4>
                            </div>
                        </div>
                    </div>
                </div>
        </div>
    )
}

export default About
