import React from 'react'
import './Home.css'
import { Link } from "react-scroll";

const index = () => {
    return (
        <div className="Page flex-column container ns" id="home">
            <div className="Text">
                Hola, me llamo <span className="name">Luis Soto</span>. <br/>
                Soy Full-Stack web developer.<br/>
            </div>
            <Link 
            to="projects"
            activeClass="active"
            spy={true}
            smooth={true}
            offset={0}
            duration={1500} 
            className="Btn">Ver mis proyectos <span className="arrow-icon">➜</span></Link>
        </div>
    )
}

export default index
